::-moz-selection {
  color: white;
  background: #ffa08d;
}

::selection {
  color: white;
  background: #ffa08d;
}

body {
  color: #2f2670;
  font-family: 'Rubik', sans-serif;
}

a {
  color: #ffa08d;
}

a:hover {
  color: #2f2670;
}

.hidden {
  display: none;
}

/* Navigation */

.navigation {
  padding-top: 1em;
  padding-bottom: 1em;
}

/* Hero */

.hero-kitten {
  background-color: #faf4cf;
}

.title {
  font-size: 4rem;
  font-weight: 700;
  color: #2f2670;
}

.subtitle {
  font-size: 3rem;
  color: #2f2670;
  position: relative;
}

.logo {
  font-size: 1.4rem;
  font-weight: 700;
  color: #2f2670;
  letter-spacing: 0.1em;
}

.subtitle:after {
  content: '';
  position: absolute;
  top: -1rem;
  right: 12rem;
  width: 6rem;
  height: 6rem;
  margin: auto;
  background-image: url(./img/ether-diamond.gif);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top;
}

/* Purrents */

.hero.is-small,
.hero-body,
.hero.is-small .hero-body {
  padding: 0;
}

/* Link */

.purr {
  border-bottom: 1px solid #f4f3fc;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

.purr--form {
  margin-top: 1.5rem;
  padding-bottom: 1.5rem;
}

.purr--message {
  font-size: 1.8rem;
  display: inline-block;
  word-break: break-all;
}

.purr--date {
  padding-left: 4rem;
  margin-left: auto;
  font-size: 0.8rem;
  letter-spacing: 0.2em;
  font-weight: 700;
  font-family: 'Open Sans', sans-serif;
  color: #827f87;
}

.changeCat--button {
  position: absolute;
  width: 40px;
  height: 40px;
  padding: 0;
  margin: 0;
  top: 50%;
  background: none;
  outline: none;
  border: none;
  -webkit-transform: translateY(calc(-50% - 5px));
      -ms-transform: translateY(calc(-50% - 5px));
          transform: translateY(calc(-50% - 5px));
  cursor: pointer;
}

.changeCat--button-back .changeCat--arrow {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
}

.changeCat--button-back:hover .changeCat--arrow {
  color: #ffa08d;
  -webkit-transform: scale(1.1) rotate(90deg);
      -ms-transform: scale(1.1) rotate(90deg);
          transform: scale(1.1) rotate(90deg);
}

.changeCat--button-forward .changeCat--arrow {
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.changeCat--button-forward:hover .changeCat--arrow {
  color: #ffa08d;
  -webkit-transform: scale(1.1) rotate(-90deg);
      -ms-transform: scale(1.1) rotate(-90deg);
          transform: scale(1.1) rotate(-90deg);
}

.kitten--img-container {
  overflow: hidden;
  display: block;
  width: 50px;
  height: 50px;
  margin: 0 auto;
  position: relative;
  border-radius: 50%;
}

.kitten img {
  width: 110px;
  position: absolute;
  left: 55%;
  top: 85%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: none;
}

.main {
  z-index: 999;
  margin-bottom: 350px; /* Same height as footer */
  background-color: white;
  position: relative;
}

/* Footer */

.fixed_footer .content {
  margin-bottom: 350px; /* Same height as footer */
}

.fixed_footer .is-size-2 {
  color: white;
  font-weight: 700;
  font-size: 4rem;
  margin-top: 2rem;
  margin-bottom: 4rem;
}

.fixed_footer {
  width: 100%;
  height: 350px;
  background: #2f2670;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: -100;
}

.fixed_footer h2 {
  font-size: 1.4rem;
  font-family: 'Rubik';
  font-weight: 700;
  color: #ffa08d;
}

.fixed_footer p {
  font-size: 1.4rem;
  color: white;
}
.arrow {
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.external:hover .arrow {
  -webkit-transform: translate(5px, -5px);
      -ms-transform: translate(5px, -5px);
          transform: translate(5px, -5px);
}

/* Form */

.group {
  position: relative;
}

input {
  font-size: 18px;
  padding: 10px 10px 10px 5px;
  display: block;
  border: none;
  width: 100%;
  border-bottom: 1px solid #2f2670;
}

input:focus {
  outline: none;
}

label {
  color: #2f2670;
  font-size: 2rem;
  font-weight: bold;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 0px;
  -o-transition: 0.2s ease all;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

input.filled ~ label,
input.empty:focus ~ label {
  top: -20px;
  font-size: 14px;
  color: #f3a491;
}

.bar {
  position: relative;
  display: block;
}
.bar:before,
.bar:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: 0px;
  position: absolute;
  background: #f3a491;
  -o-transition: 0.2s ease all;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

.bar:before {
  left: 50%;
}
.bar:after {
  right: 50%;
}

.description {
  margin-top: 3rem;
}

input:focus ~ .bar:before,
input:focus ~ .bar:after {
  width: 50%;
}

.highlight {
  position: absolute;
  height: 60%;
  width: 100px;
  top: 25%;
  left: 0;
  pointer-events: none;
  opacity: 0.5;
}

input:focus ~ .highlight {
  -webkit-animation: inputHighlighter 0.3s ease;
  animation: inputHighlighter 0.3s ease;
}

@-webkit-keyframes inputHighlighter {
  from {
    background: #5264ae;
  }
  to {
    width: 0;
    background: transparent;
  }
}
@keyframes inputHighlighter {
  from {
    background: #5264ae;
  }
  to {
    width: 0;
    background: transparent;
  }
}

.purrit {
  margin-top: 3rem;
  font-size: 1.4rem;
  padding: 1.6em 2em;
  font-family: 'Rubik';
  font-weight: 700;
  letter-spacing: 0.2em;
  background-color: #ffe0da;
  color: #f5907c;
  border: 0;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.purrit:hover {
  color: #f5907c;
  cursor: url(img/paw.svg), auto;
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
  -webkit-box-shadow: 2px 15px 35px 0px rgba(252, 222, 222, 0.65);
  box-shadow: 2px 15px 35px 0px rgba(252, 222, 222, 0.65);
}

.your-kitten {
  position: relative;
}


